body {
    color: #3a3b3c;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    font-size: 1em;
    margin-top: 2em;
    margin-left: 2em;
}

h2 {
    text-align: center;
    font-size: 2em;
    font-weight: 700;
}

button {
    background: #006098;
    color: white;
    border-style: outset;
    border-radius: 5px;
    padding: 5px 10px;
    border-color: #006098;
    text-shadow: none;
}

input {
    padding: 8px 10px;
    margin: 5px;
    border: none;
    box-shadow: 0 0 15px 4px rgba(0, 0, 0, 0.107);
    border-radius: 5px;
    box-shadow: black;
}

li {
    color: grey;
    padding-top: 3px;
    font-size: 15px;
}

.error {
    color: red;
    background: lightgrey;
    font-size: 20px;
    border-style: solid;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
}
.success {
    color: green;
    background: lightgrey;
    font-size: 20px;
    border-style: solid;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
}

.loginform {
    text-align: center;
}
